import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { WinGame, AppState } from '../../common/components/types';

const initialState: AppState = {
  name: 'Name',
  isPlayed: false,
  isSelected: false,
  isWinner: false,
  winners: [],
  isWinnersVisible: false,
}

const slice = createSlice({
  name: 'game',
  initialState,
  reducers: {
    setIsPlayed(state, action: PayloadAction<boolean>) {
      state.isPlayed = action.payload;
    },
    setName(state, action: PayloadAction<string>) {
      state.name = action.payload;
    },
    setIsSelected(state, action: PayloadAction<boolean>) {
      state.isSelected = action.payload;
    },
    setIsWinner(state, action: PayloadAction<boolean>) {
      state.isWinner = action.payload;
    },
    setWinners(state, action: PayloadAction<WinGame[]>) {
      state.winners = action.payload;
    },
    setIsWinnersVisible(state, action: PayloadAction<boolean>) {
      state.isWinnersVisible = action.payload;
    },
    reset(state) {
      state.isPlayed = false;
      state.isSelected = false;
      state.isWinner = false;
      state.name = '';
    }
  }
});

export const { actions, reducer } = slice;