import React, { FC, Suspense, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Redirect,
} from 'react-router-dom';
import { START_ROUTE } from './app/common/components/constants';
import { WinGame } from './app/common/components/types';
import routes, { RoutProps } from './app/routes';
import { actions } from './app/store/game/slice';

const RenderRoute = ({
  component: Component,
  needsAuth,
  title,
  ...rest
}: RoutProps) => {

  return (
    <Suspense fallback={null}>
      <Route
        {...rest}
        render={props => {
          <Redirect to={START_ROUTE} />
          return <Component {...props} />;
        }}
      />
    </Suspense>
  );
};

const App: FC = () => {
  const dispatch = useDispatch();

  useEffect(() => {
    const winnersString = localStorage.getItem('winners');
    if (winnersString) {
      const winners: WinGame[] = JSON.parse(winnersString);
      dispatch(actions.setWinners(winners));
    }
  }, [dispatch]);
  
  return (
    <Router>
      <Switch>
        {routes.map((props, index) => (
          <RenderRoute key={`router-${index}`} {...props} />
        ))}
        <Redirect to={START_ROUTE} />
      </Switch>
    </Router>
  );
};

export default App;
