import { RouteProps } from 'react-router-dom';
import { ElementType, lazy } from 'react';

const StartComponent = lazy(() => import('./start'));
const StartComponentPt = lazy(() => import('./start-pt'));
const GameComponent = lazy(() => import('./game/'));
const GameComponentPt = lazy(() => import('./game-pt'));

export interface RoutProps extends Omit<RouteProps, 'component'> {
  needsAuth: boolean;
  component: ElementType;
  title: string;
}

const routes = [
  {
    title: 'Gianny\'s pizza - Start',
    path: '/en/start',
    exact: true,
    component: StartComponent,
    needsAuth: false,
  },
  {
    title: 'Gianny\'s pizza - Game',
    path: '/en/game',
    exact: true,
    component: GameComponent,
    needsAuth: false,
  },
  {
    title: 'Gianny\'s pizza - Começar',
    path: '/pt/start',
    exact: true,
    component: StartComponentPt,
    needsAuth: false,
  },
  {
    title: 'Gianny\'s pizza - Jogo',
    path: '/pt/game',
    exact: true,
    component: GameComponentPt,
    needsAuth: false,
  },
];

export default routes;
